import React, { useState, useEffect } from 'react';
import { getAvailableTimes, makeReservation } from '../services/reservation';
import ReservationForm from '../components/ReservationForm';
import Sidebar from '../components/Sidebar';

const Agendamento: React.FC = () => {
  const [availableTimes, setAvailableTimes] = useState<any[]>([]);
  const [error, setError] = useState('');
  const [environmentId, setEnvironmentId] = useState<number | null>(null);
  const [loading, setLoading] = useState(false);

  // Mocking data for available times
  const mockAvailableTimes = [
    { id: 1, time: '10:00', available: true },
    { id: 2, time: '11:00', available: true },
    { id: 3, time: '12:00', available: false },
    { id: 4, time: '14:00', available: true },
  ];

  const environments = [
    { id: 1, name: 'Biblioteca', description: 'Ambiente silencioso para leitura', icon: 'üìö' },
    { id: 2, name: 'Salas', description: 'Salas para reuni√£o', icon: 'üõãÔ∏è' },
    { id: 3, name: 'AlphaLAB', description: 'Espa√ßo para inova√ß√£o e prototipagem', icon: 'üí°' },
    { id: 4, name: 'Audit√≥rio', description: 'Espa√ßo para palestras e eventos', icon: 'üé§' }
  ];

  useEffect(() => {
    const fetchAvailableTimes = () => {
      setLoading(true);
      try {
        const response = { data: mockAvailableTimes };
        setAvailableTimes(response.data);
        setLoading(false);
      } catch (err) {
        setError('Erro ao carregar os hor√°rios dispon√≠veis');
        setLoading(false);
      }
    };

    if (environmentId !== null) {
      fetchAvailableTimes();
    }
  }, [environmentId]);

  const handleReservation = async (data: { date: string; time: string }) => {
    try {
      await makeReservation(data);
      setError('');
      alert('Reserva realizada com sucesso');
    } catch (err) {
      setError('Erro ao realizar a reserva');
    }
  };

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px' }}>
        <h1>Agendar Reserva</h1>

        <div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
          {environments.map((env) => (
            <div
              key={env.id}
              onClick={() => setEnvironmentId(env.id)}
              style={{
                cursor: 'pointer',
                padding: '20px',
                border: '2px solid',
                borderColor: environmentId === env.id ? 'blue' : 'gray',
                borderRadius: '8px',
                textAlign: 'center',
                width: '150px',
                transition: 'all 0.3s ease',
                boxShadow: environmentId === env.id ? '0 4px 10px rgba(0, 0, 255, 0.2)' : '',
              }}
              title={env.description}
            >
              <div style={{ fontSize: '30px' }}>{env.icon}</div>
              <h3>{env.name}</h3>
              <p>{env.description}</p>
            </div>
          ))}
        </div>

        {error && <p style={{ color: 'red' }}>{error}</p>}

        {loading ? (
          <div>Carregando...</div>
        ) : availableTimes.length > 0 ? (
          <ReservationForm availableTimes={availableTimes} onReserve={handleReservation} />
        ) : (
          <p>Selecione um ambiente para ver os hor√°rios dispon√≠veis.</p>
        )}
      </div>
    </div>
  );
};

export default Agendamento;
